*, *::before, *::after {
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
  font: inherit;
}

html {
  font-size: 1.125rem;
  box-sizing: border-box;
  font-family: "Epilogue", sans-serif;
  font-weight: 500;
}

body {
  min-height: 100vh;
  background-color: hsl(0deg, 0%, 98%);
}
@media (min-width: 64em) {
  body {
    padding: 1.625rem 2.25rem;
  }
}

ul {
  list-style: none;
}

img, picture, svg, video {
  display: block;
  height: auto;
  max-width: 100%;
}

iframe {
  border: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td, th {
  padding: 0;
}

@media (max-width: 63.9375em) {
  .hidden-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hidden-for-desktop {
    display: none;
  }
}

.todoList {
  padding-left: 1.75rem;
  margin-top: 3px;
}
.todoList::before {
  content: "";
  background-image: url("/images/icon-todo.svg");
  width: 0.9375rem;
  height: 1.0625rem;
}
@media (min-width: 64em) {
  .todoList::before {
    width: 0.875rem;
    height: 1rem;
  }
}

.calendar {
  padding-left: 1.75rem;
}
.calendar::before {
  content: "";
  background-image: url("/images/icon-calendar.svg");
  width: 1.0625rem;
  height: 1.25rem;
}
@media (min-width: 64em) {
  .calendar::before {
    width: 1rem;
    height: 1.1875rem;
  }
}

.reminders {
  padding-left: 1.75rem;
}
.reminders::before {
  content: "";
  background-image: url("/images/icon-reminders.svg");
  width: 0.875rem;
  height: 1.125rem;
  left: 0.125rem !important;
}
@media (min-width: 64em) {
  .reminders::before {
    width: 0.8125rem;
    height: 1.0625rem;
  }
}

.planning {
  padding-left: 1.75rem;
}
.planning::before {
  content: "";
  background-image: url("/images/icon-planning.svg");
  width: 1.0625rem;
  height: 1.0625rem;
}
@media (min-width: 64em) {
  .planning::before {
    width: 1rem;
    height: 1rem;
  }
}

.show {
  visibility: visible !important;
  opacity: 1 !important;
}

.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1, h2, h3 {
  margin: 0;
}

p {
  margin: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 4.375rem;
  margin-bottom: 1.125rem;
}
@media (min-width: 64em) {
  .main {
    flex-direction: row-reverse;
    margin: 0;
    padding: 1.875rem 3.125rem;
    width: 100%;
  }
}
.main__img {
  margin-bottom: 2rem;
}
.main__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.75rem;
  text-align: center;
  max-width: 43.375rem;
}
.main__content h1 {
  font-size: 1.875rem;
  font-weight: 700;
  margin-bottom: 1rem;
}
.main__content p {
  font-size: 0.875rem;
  line-height: 1.6;
  color: hsl(0deg, 0%, 41%);
  margin-bottom: 1.125rem;
}
.main__content button {
  background-color: rgb(20, 20, 20);
  border: 1px solid rgb(20, 20, 20);
  font-size: 0.875rem;
  font-weight: 700;
  color: hsl(0deg, 0%, 98%);
  padding: 0.875rem 1.25rem;
  border-radius: 0.75rem;
  margin-bottom: 2.5rem;
  cursor: pointer;
}
.main__content button:hover {
  background-color: hsl(0deg, 0%, 98%);
  border: 1px solid rgb(20, 20, 20);
  color: rgb(20, 20, 20);
}
@media (min-width: 64em) {
  .main__content {
    text-align: left;
    align-items: flex-start;
    padding: 0;
    margin-left: 2.5rem;
    margin-right: 5rem;
  }
  .main__content h1 {
    font-size: 3.875rem;
    margin-bottom: 1.875rem;
  }
  .main__content p {
    font-size: 1rem;
    margin-bottom: 1.875rem;
  }
  .main__content button {
    margin-bottom: 5rem;
  }
}
.main__logos {
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 8%;
  max-width: 31.25rem;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  font-size: 0.8125rem;
}
@media (max-width: 63.9375em) {
  .header {
    padding: 1rem 0.75rem;
    position: fixed;
    top: 0;
    background-color: hsl(0deg, 0%, 98%);
  }
}
.header__left {
  display: flex;
  align-items: center;
}
.header__left .logo {
  display: inline-block;
  margin-right: 3.375rem;
  cursor: pointer;
}
.header__navigation {
  display: flex;
}
.header__navigation a {
  color: hsl(0deg, 0%, 41%);
}
.header__navigation a:hover {
  color: rgb(20, 20, 20);
}
.header__navigation .a2:not(:last-child) {
  margin-right: 2rem;
}
.header__navigation .container {
  margin-right: 2rem;
}
.header__navigation .container .btn {
  display: flex;
  align-items: center;
  color: hsl(0deg, 0%, 41%);
  border: none;
  background: none;
  cursor: pointer;
  position: relative;
}
.header__navigation .container .btn:hover {
  color: rgb(20, 20, 20);
}
.header__navigation .container .btn:hover .icon-arrow-down {
  background-image: url(/images/icon-arrow-down-2.svg);
}
.header__navigation .container .btn[aria-expanded=true] {
  color: rgb(20, 20, 20);
}
.header__navigation .container .btn .icon-arrow-down {
  width: 0.625rem;
  height: 0.375rem;
  background-repeat: no-repeat;
  background-image: url(/images/icon-arrow-down.svg);
  margin-left: 0.375rem;
}
.header__navigation .container .btn .icon-arrow-down[aria-expanded=true] {
  background-image: url(/images/icon-arrow-up-black.svg);
}
.header__navigation .container .dropdown {
  background-color: hsl(0deg, 0%, 98%);
  padding: 1.25rem;
  border-radius: 0.625rem;
  margin-top: 1rem;
  box-shadow: 0 10px 40px 0px rgba(0, 0, 0, 0.15);
  position: absolute;
  visibility: hidden;
  opacity: 0;
}
.header__navigation .container .dropdown__1 {
  left: 6.5rem;
}
.header__navigation .container .dropdown__2 .a {
  padding: 0;
}
.header__navigation .container .dropdown a {
  display: flex;
  padding-left: 1.625rem;
  position: relative;
}
.header__navigation .container .dropdown a:not(:last-child) {
  margin-bottom: 1rem;
}
.header__navigation .container .dropdown a::before {
  background-repeat: no-repeat;
  position: absolute;
  top: -0.125rem;
  left: 0px;
  background-size: contain;
  display: inline-block;
}
.header__navigation .container .dropdown a:hover {
  color: rgb(20, 20, 20);
}
.header__right a {
  color: hsl(0deg, 0%, 41%);
  margin-right: 1.75rem;
}
.header__right a:hover {
  color: rgb(20, 20, 20);
}
.header__right button {
  color: hsl(0deg, 0%, 41%);
  border: 1px solid hsl(0deg, 0%, 41%);
  background: none;
  padding: 0.625rem 1.125rem;
  border-radius: 0.875rem;
  cursor: pointer;
}
.header__right button:hover {
  border: 1px solid rgb(20, 20, 20);
  color: rgb(20, 20, 20);
}
.header__hamburguerMenu-btn {
  background-image: url("/images/icon-menu.svg");
  background-repeat: no-repeat;
  height: 18px;
  width: 32px;
  position: absolute;
  z-index: 999;
  top: 1.125rem;
  right: 1rem;
  cursor: pointer;
}
.header__hamburguerMenu-navigation {
  position: fixed;
  background-color: hsl(0deg, 0%, 98%);
  padding: 4.375rem 1.25rem;
  height: 100vh;
  width: 66%;
  max-width: 20rem;
  top: 0;
  right: 0;
  color: hsl(0deg, 0%, 41%);
  transform: translateX(100%);
  transition: transform 150ms ease-in-out;
}
.header__hamburguerMenu-navigation a {
  color: hsl(0deg, 0%, 41%);
}
.header__hamburguerMenu-container {
  position: relative;
}
.header__hamburguerMenu-container[aria-expanded=true] .header__hamburguerMenu-label::after {
  content: url("/images/icon-arrow-up.svg");
}
.header__hamburguerMenu-container[aria-expanded=true] .header__hamburguerMenu-content {
  max-height: 9.375rem;
}
.header__hamburguerMenu-label {
  position: relative;
  margin-bottom: 1.375rem;
  cursor: pointer;
}
.header__hamburguerMenu-label::after {
  content: url("/images/icon-arrow-down.svg");
  margin-left: 0.625rem;
}
.header__hamburguerMenu-content {
  display: flex;
  flex-direction: column;
  margin-left: 1rem;
  max-height: 0;
  overflow: hidden;
  transition: 300ms ease-in-out;
}
.header__hamburguerMenu-content a {
  position: relative;
}
.header__hamburguerMenu-content a:last-child {
  margin-bottom: 1.375rem;
}
.header__hamburguerMenu-content a::before {
  background-repeat: no-repeat;
  position: absolute;
  top: -0.125rem;
  left: 0px;
  background-size: contain;
  display: inline-block;
}
.header__hamburguerMenu-content a:not(:last-child) {
  margin-bottom: 1.125rem;
}
.header__hamburguerMenu-a {
  display: block;
}
.header__hamburguerMenu-a:not(:last-of-type) {
  margin-bottom: 1.375rem;
}
.header__hamburguerMenu-a:last-of-type {
  margin-bottom: 2.125rem;
}
.header__hamburguerMenu-loginSection {
  display: flex;
  flex-direction: column;
}
.header__hamburguerMenu-loginSection button {
  color: hsl(0deg, 0%, 41%);
  cursor: pointer;
  background: none;
  padding: 0.625rem 0;
  border: none;
}
.header__hamburguerMenu-loginSection button:last-child {
  border: 1px solid hsl(0deg, 0%, 41%);
  border-radius: 0.75rem;
  margin-top: 0.375rem;
}
.header__hamburguerMenu .overlay {
  visibility: hidden;
  opacity: 0;
  position: fixed;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  background-color: rgb(0, 0, 0);
  transition: 600ms ease-in-out;
}
.header__hamburguerMenu[menu-open=true] .overlay {
  visibility: visible;
  opacity: 0.5;
}
.header__hamburguerMenu[menu-open=true] .header__hamburguerMenu-btn {
  background-image: url("/images/icon-close-menu.svg");
  height: 26px;
  width: 26px;
  top: 1rem;
}
.header__hamburguerMenu[menu-open=true] .header__hamburguerMenu-navigation {
  transform: translateX(0%);
}